/**
 * @file
 * 
 */
// @flow

import { Snake } from "./Snake";
import { Ground } from "./Ground";
import { 
    Up,
    Down,
    Left,
    Right,
    Direction
 } from "./Direction";
import { Cell } from "./Cell";
import { Food } from "./Food";
import { Print } from "./Print";

export class Game {
    /**
     * 
     * @private
     * @type {Snake}
     * @memberof Game
     */
    $snake: Snake;

    /**
     * 
     * @private
     * @type {Ground}
     * @memberof Game
     */
    $ground: Ground;

    /**
     * 
     * @private
     * @type {Print}
     * @memberof Game
     */
    $print: Print;

    /**
     * 
     * @private
     * @type {Array<Food>}
     * @memberof Game
     */
    $food: Array<Food> = [];

    constructor({
        width= 10,
        height= 10,
        headerPosition,
        direction,
        bodyLength= 3,
    }:{
        width?: number,
        height?: number,
        headerPosition?: Cell,
        direction?: Direction,
        bodyLength?: number,
    }) {
        //
        if(direction){
            direction = Up
        }

        //
        if(!headerPosition){
            headerPosition = {
                x: Math.floor( width / 2 ),
                y: Math.floor( height / 2 ),
            }
        }

        //
        this.$snake = new Snake({
            headerPosition: headerPosition,
            direction: direction,
            bodyLength: bodyLength,
        })

        //
        this.$ground = new Ground(width, height)

        //
        this.$print = new Print(width, height)

        //
        this.$createFood(3)
    }

    /**
     * 
     */
    frame(): Array<Array<String>>{

        var eatFood = this.$snake.move(this.$food)

        if(eatFood){
            //
            this.$food = this.$food.filter(item => item != eatFood)
            this.$createFood();
        }

        this.$print.rest();
        this.$print.printHead(this.$snake.header, this.$snake.direction)
        this.$print.printBody(this.$snake.header, this.$snake.body)
        this.$print.printFoot(this.$food)

        return this.$print.cells
    }

    /**
     * 
     * @param willTurnDirection     
     */
    turn(willTurnDirection: Direction){
        this.$snake.turn(willTurnDirection)
    }

    /**
     * 
     */
    $createFood(count = 1){
        const cells = this.$ground.getRandomUsableCells(count, this.$food, this.$snake.header, this.$snake.body);
        cells.forEach(cell => {
            this.$food.push(new Food(cell.x, cell.y, Math.floor(Math.random() * 3) + 1));
        })
    }
}



